.container{
    margin-top: 100px;

}

.headerText{
    max-width: 270px;
    width: 100%;
    margin: 0 auto;
}

.headerText h1{
    color: rgb(24, 24, 24);
    font-family: PP Neue Montreal;
    font-size: 33px;
    font-weight: 500;
    line-height: 100%;
    letter-spacing: 0.6px;
    text-align: center;
}

.ThirdPartiesAuthContainer{
    display: inline-flex;
    flex-direction: column;
    gap: 15px;
}

.orText{
    display: inline-flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    border: none;
    align-items: center;
    margin: 10px 0;
}

.orText span{
    padding: 10px;
}

.orText hr{
    border: 0;
    width: 100%;
    height: 1px;
    background-color: #5b5656;
}

.lowerText{
    margin-top: 16px;
    font-family:PP Neue Montreal;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0.8px;
    text-align: center;
}

.makeYellow{
    color: rgb(254, 194, 0);
    cursor: pointer;
}

.forgotKeepLogin{
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    justify-content: space-between;
}

.Keepmeloggedin{
    display: inline-flex;
    flex-direction: row;
    align-items: center;
}

.checkBoxtext, .lowerText2{
    color: rgb(24, 24, 24);
    font-family: PP Neue Montreal;
    font-size: 12px;
    font-weight: 400;
    line-height: 40px;
    letter-spacing: 0.6px;
    text-align: center;
}

.forgotPword{
    color: rgb(254, 194, 0);
    cursor: pointer;
    font-family: PP Neue Montreal;
    font-size: 12px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.6px;
    text-align: center;
    text-decoration: none;
}

.headEmail{
    color: rgb(24, 24, 24);
    font-family:PP Neue Montreal;
    font-size: 16px;
    font-weight: 400;
    margin: 0;
    line-height: 40px;
    letter-spacing: 0.6px;
    text-align: center;
}

@media (min-width: 765px) {
    .container{
        margin-top: 172px;
    }
    .headerText{
        max-width: 506px;
        width: 100%;
        margin: 0 auto;

    }

    .forgotPword{
        color: rgb(254, 194, 0);
    }

    .forgotPword, .checkBoxtext .lowerText2{
        font-size: 14px;
    }

    .headerText h1{
        font-size: 36px;
    }
}


@media (min-width: 1024px) {
    .container{
        margin-top: 200px;
    }

    .headerText{
        max-width: 533px;
        width: 100%;
        margin: 0 auto;

    }

    .headerText h1{
        font-size: 36px;
    }
}