.container{
    margin-top: 100px;
    transition: all 0.3s ease-out;

}


.lowerText{
    margin-top: 16px;
    font-family:PP Neue Montreal;
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0.8px;
    text-align: center;
    margin-bottom: 20px;
}

.stageContainer{
    max-width: 290px;
    margin: 0 auto;
}


.stageContainer h1{
    color: rgb(24, 24, 24);
    font-family:PP Neue Montreal;
    font-size: 24px;
    font-weight: 500;
    line-height: 40px;
    letter-spacing: -1px;
    text-align: center;
}

.stageContainer p{
    color: rgb(98, 100, 106);
    font-family:PP Neue Montreal;
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0.6px;
    text-align: center;
}



@media (min-width: 765px) {
    .container{
        margin-top: 172px;
    }

    .stageContainer{
        max-width: 504px;
    }

    .lowerText{
        margin-top: 16px;
        font-family:PP Neue Montreal;
        font-size: 14px;
        font-weight: 400;
        line-height: 140%;
        letter-spacing: 0.8px;
        text-align: center;
        margin-bottom: 20px;
    }
}

@media (min-width: 1024px) {
    .container{
        margin-top: 200px;
    }

    .stageContainer{
        max-width: 550px;
    }

    .lowerText{
        margin-top: 16px;
        font-family:PP Neue Montreal;
        font-size: 16px;
        font-weight: 400;
        line-height: 140%;
        letter-spacing: 0.8px;
        text-align: center;
        margin-bottom: 20px;
    }
}