.ctn {
    margin: auto;
    margin-top: 150px;
    padding-bottom: 40px;
    /* padding: 0px 0px 40px 80px; */
    width: 60.75dvw;
}

.settingPages {
    display: flex;
    align-items: center;
    gap: 10px;
}

.backToSettings,
.currentPage {
    display: flex;
    justify-content: start;
    align-items: center;
    font-size: 16px;
    font-weight: 500;
    gap: 15px;
}

.backToSettings {
    text-decoration: none;
    color: #000000;
}

.currentPage {
    color: #97979D;
}

.notify_sect {
    margin-top: 30px;
    text-align: left;
}

.notify_title {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}

.jobNotify_title {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 30px;
    gap: 10px;
}

h5 {
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 0.32px;
    color: #000000;
    margin-bottom: 0px;
}

.notify_title p,
.jobNotify_title p {
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 0.32px;
    color: #000000;
    margin: 0px;
}


.notify_info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 15px;
}

.notify_info p {
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.32px;
    color: #797979;
}

.notify_alert {
    margin-top: 25px;
    padding-left: 15px;
}

.notify_alert h5 {
    margin-top: 15px;
    font-weight: 400;
    font-size: 16px;
    letter-spacing: 0.32px;
    color: black;
}

.notify_alert p {
    font-weight: 500;
    font-size: 14px;
    letter-spacing: 0.32px;
    color: #797979;
}

.switch {
    font-weight: 400;
    font-size: 13px;
    color: #1E1E1E;
    letter-spacing: 0.32px;
}

@media (max-width: 768px) {

    .ctn {
        margin-top: 130px;
        /* padding: 0px 0px 40px 30px; */
        width: 80.75dvw;
    }

    .backToSettings,
    .currentPage {
        font-size: 14px;
    }

    .notify_title p,
    .jobNotify_title p {
        font-size: 14px;
    }

    h5 {
        font-size: 14px;
    }

    .notify_alert {
        margin-top: 15px;
    }

    .notify_alert h5 {
        font-size: 14px;
    }

    .notify_alert p {
        margin-bottom: 8px;
        font-size: 14px;

    }
}

@media (max-width: 320px) {

    .ctn {
        margin: auto;
        margin-top: 80px;
        width: 90dvw;
    }

    .notify_sect {
        margin-top: 15px;
    }

    h5 {
        font-size: 13.6px;
        margin-top: 15px;
    }

    .notify_info {
        gap: 30px;
    }

    .notify_info p {
        font-size: 13px;
        margin-top: 8px;
        line-height: 22px;
    }

    .jobNotify_title {
        margin-top: 25px;
    }

    .switch {
        font-size: 12px;
    }

    .notify_alert p {
        font-size: 13px;
        margin-top: 8px;
        line-height: 22px;
    }
    
}